.darkBG {
  background-color: rgba(0, 0, 0, 0.3);
  width: 100vw;
  height: 101vh;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: fixed;
  backdrop-filter: blur(5px); }

.centered {
  position: fixed;
  z-index: 9999;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.modal {
  color: white;
  max-height: 90vh;
  z-index: 9999;
  border-radius: 16px;
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.04); }
  @media (max-width: 1150px) {
    .modal {
      max-width: 90vw;
      height: auto; } }

.closeBtn {
  cursor: pointer;
  font-weight: 500;
  border: none;
  width: 30px;
  height: 30px;
  background: url("../../../assets/UI/closeButton.svg") no-repeat center;
  transition: all 0.25s ease;
  position: absolute;
  right: 0;
  top: 0;
  margin-top: 10px;
  margin-right: 10px;
  z-index: 10; }
  @media (max-width: 550px) {
    .closeBtn {
      width: 20px;
      height: 20px; } }

.closeBtn:hover {
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.04);
  transform: scale(1.1); }

.modalContent .modalBgFix {
  width: 90vw;
  height: 90vh; }

.modalWrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  position: relative; }

.modalImg {
  margin: 0;
  border-radius: 16px;
  max-height: 90vh; }
  @media (max-width: 1150px) {
    .modalImg {
      max-width: 90vw;
      height: auto; } }

.modalHeader {
  position: absolute;
  top: 0;
  font-size: clamp(0.8rem, calc(0.2rem + 1vw), 1rem);
  text-align: center;
  padding: 15px 0;
  margin: auto 0; }

.modalNumber {
  font-size: clamp(0.8rem, calc(0.2rem + 1vw), 1rem);
  position: absolute;
  bottom: 15px;
  left: 15px; }

.modalPrice {
  font-size: clamp(0.8rem, calc(0.2rem + 1vw), 1rem);
  text-align: center;
  padding: 5px 0;
  position: absolute;
  bottom: 15px; }
